
import engine from "engine";
@engine.decorators.serialize("init")
export default class init extends engine.Script {
    @engine.decorators.property({
        type: engine.TypeNames.String
    })
    public name: string = "myname"

    public onAwake() {
        console.log('we are here');
        const album: string[] = [
            'musics/1.mp3',
            'musics/2.mp3',
            'musics/3.mp3',
        ];
        // 马良 _ 孙茜茹 - 往后余生
        for (let song of album) {
            engine.loader.load(song).promise.then(_ => {
                console.log(song, ' loaded');
            })
        }
    }
    public onUpdate(dt) {

    }
    public onDestroy() {

    }
}
